// ==========================================================================
// Bar section
// ==========================================================================

.l-bar {
  background-color: $white;
  position: fixed;
  bottom: 0;
  left: 0;
  box-shadow: rgba($black, 0.3) 0 1px 12px;
  z-index: 120;
  display: none;

  .content {
    height: 100%;
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    max-width: 1024px !important;

    h5 {
      margin: 1rem auto 0;
    }
  }

  .btn {
    height: 55px;
    margin: 1em auto;
  }
}

@include media-breakpoint-up(md) {
  .l-bar {
    .content {
      flex-wrap: wrap;

      h5 {
        margin: 1.15em auto 0;
        display: inline;
      }
    }
  }
}
