// ==========================================================================
// Helper classes and tags
// ==========================================================================

// Small
// -------------------------

small,
small a,
small p {
  font-size: 80%;
  line-height: inherit;
}

// Mute
// -------------------------

.text-muted {
  color: $grey-default;
}

// Accessibility
// -------------------------

.sr-only {
  display: none;
}

// Text-Align
// -------------------------

.text-left {
  text-align: left;
}

.text-xs-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-justify {
  text-align: justify;
}

@include media-breakpoint-up(sm) {
  .text-sm-right {
    text-align: right;
  }
}

@include media-breakpoint-up(md) {
  .text-md-left {
    text-align: left;
  }
}

@include media-breakpoint-up(xl) {
  .text-xl-left {
    text-align: left;
  }
}

.cursor {
  cursor: pointer;
}

