// ==========================================================================
// Testimonial section
// ==========================================================================

.l-testimonials {
  background-color: $white;

  .wrapper {
    margin-bottom: 2em;
    margin-top: 2em;
  }

  .d-flex {
    justify-content: space-between;
    flex-wrap: wrap;
  }

  article,
  article.d-flex {
    background-color: $grey-bright;
    box-shadow: 0 70px 0 0 $white inset;
    width: 100%;
    padding: 1rem;
    box-sizing: border-box;
    display: flex;
    justify-content: inherit;
  }

  img {
    width: 140px;
  }

  blockquote {
    width: 90%; // ie fix
  }
}

@include media-breakpoint-up(md) {
  .l-testimonials {
    article,
    article.d-flex {
      box-shadow: 0 100px 0 0 $white inset;
      width: 32%;
    }

    img {
      width: 200px;
    }

    blockquote {
      min-height: 180px;
    }
  }
}
