// ==========================================================================
// Forms > Select field, e.g. used in product-template.
// Based on http://wtfforms.com
// ==========================================================================

.form-group {
  margin-bottom: 1rem;
  margin-top: 1rem;

  .label--input {
    @extend h6;
  }

  &.append-btn {
    position: relative;

    input,
    .btn {
      line-height: 2;
      padding: 0.5rem 1rem;
    }

    input {
      position: relative;
      border-color: $white;
      z-index: 100;
    }

    .btn {
      background-color: $grey-decent;
      border: 1px solid $grey-decent;
      border-bottom-left-radius: 0;
      border-top-left-radius: 0;
      position: absolute;
      right: 0;
      top: 0;
      z-index: 110;
    }
  }
}

input {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: $white;
  border-radius: 4px;
  border: 1px solid;
  color: $grey-dark;
  display: inline-block;
  line-height: 2.25;
  margin: 0;

  &[disabled] {
    background-color: $grey-decent;
    cursor: default;
  }
}

input[type="text"],
input[type="number"],
input[type="tel"],
input[type="email"] {
  box-sizing: border-box;
  cursor: pointer;
  height: 54px;
  padding: 0.5em 1em;
  width: 100%;
}

